import React,{useContext,useState, useCallback} from 'react'
import { Card, Row, Col } from 'react-bootstrap'
import { debounce } from "lodash-es";
import {AppContext} from '../../../../Resources/CanvasContextFile';
import {isColor} from "../../../../Resources/globalFunctions";
import ControlsMap from '../../../Properties/PropertiesConfiguration'
import StepPropertiesJson from "../../../Properties/StepPropertiesJson";
import CollapsibleComponent from '../../../../ReusableComponents/CollapseCard/CollapseCard';
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker"
import ToggleBtn from '../../../../ReusableComponents/ToggleBtn/ToggleBtn'
import ImageUploadBtn from '../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn'
import ControlLabel from '../../../../ReusableComponents/ControlLabel/ControlLabel'
import ImageRenderer from '../../../../ReusableComponents/ImageRenderer'
import {createUniqueId, sendRequestForControl, updateStepIndex} from "../../IFormDesignerLogic"

import SvgDeleteIcon from "../../../../Icons/DeleteIcon"
import SvgStyle1 from '../../../../Icons/SectionIcons/style1'
import SvgStyle2 from '../../../../Icons/SectionIcons/style2'
import SvgStyle3 from '../../../../Icons/SectionIcons/style3'
import SvgStyle4 from '../../../../Icons/SectionIcons/style4'
import SvgAlignment1 from '../../../../Icons/Image/alignment1'
import SvgAlignment2 from '../../../../Icons/Image/alignment2'
import SvgAlignment3 from '../../../../Icons/Image/alignment3'
import PlusOrange from '../../../../Icons/PlusOrange'
import RedCross from '../../../../Icons/RedCross'
import ActiveIcon from "../../../../ReusableComponents/ActiveIcon/ActiveIcon";
import TabColumn1 from "../../../../Icons/TabColumn1";
import TabColumn2 from "../../../../Icons/TabColumn2";
import TabColumn3 from "../../../../Icons/TabColumn3";
import TabColumn4 from "../../../../Icons/TabColumn4";
import TabColumn5 from "../../../../Icons/TabColumn5";
import TabColumn6 from "../../../../Icons/TabColumn6";
import CollapsibleArrowDown from "../../../../Icons/CollapsibleArrowDown";
import CollapsibleArrowUp from "../../../../Icons/CollapsibleArrowUp";
import SvgImport from "../../../../Icons/SectionIcons/importSection"
import SvgUpload from "../../../../Icons/SectionIcons/uploadControls"
import { useTranslation } from 'react-i18next'
import {useDispatch,addContexts,useSelector} from "../../../../Redux"
import CustomizedAlert from '../../../../ReusableComponents/Alert/CustomizedAlert';
import { useEffect } from 'react';
import { cloneDeep } from 'lodash-es';
import { GetStateFromData } from '../../../CustomHooks/CustomHooks';


function StepProperties(props){

  const contexts = GetStateFromData(props.containerId);
	  const {updateJson, changeSectionLayout, formState2, setOpenLoader, deleteControl,setQuickToolsClose,customControlIdList,setCustomControlIdList, setOpenProperties }=contexts.contexts;
	  const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson;      
    
    // const { updateJson,changeSectionLayout,formState2,deleteControl  } = useContext(AppContext);
    const  globalTheme = formState2?.theme?.SectionStyle;
    // const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson;
    const style=props.selectedControl.style;
    var CollapsibleItems = [];
    const { t } = useTranslation(['styles', 'translation']);

    const [activeStep,setActiveStep]=useState(props.selectedControl?.children[0]?.id);
    const [toAddSubStep,setToAddSubStep] = useState(false);

    const [confirmDeleteStep, setConfirmDeleteStep] = useState(false);
    const [confirmDeleteSubStep, setConfirmDeleteSubStep] = useState(false);

    const[alertData, setAlertData] = useState({
        open: false,
        showConfirm: "showNotification"
    });

    Object.keys(StepPropertiesJson).forEach((key, index) => {
        var collapseComponentBody = [];

        Object.keys(StepPropertiesJson[key]).forEach((property) => {

                const fetchedProperty=property.replace(/\d+/g, '');
                //used to remove any numbers from property
                collapseComponentBody.push(returnHtml(fetchedProperty,(StepPropertiesJson[key])[property], ControlsMap[fetchedProperty].type));
        })
        CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={key} body={collapseComponentBody} />);
    })

    function handleProperty(propertyName, propertyValue){
        const temp_style = {...style};
        temp_style[propertyName] = propertyValue;
        props.selectedControl.style[propertyName] = propertyValue;
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    function handleStyleType(property,value){
        // console.log(value);
        // console.log(property);
        handleProperty(property,value);
    }
    function handleSupProperty(propertyName, propertyValue){
        // const temp_style = style;
        // temp_style[propertyName] = propertyValue;
        if(propertyName==="label"){
            props.selectedControl[propertyName] = propertyValue;
        } else {
            props.selectedControl.style[propertyName] = propertyValue;
        }
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    function addOptionsFunc(){
        const oldOptionsArr=props.selectedControl.options||[];
        const newOptionsArr=[...oldOptionsArr,{"label":"","value":""}];
        props.selectedControl.options = newOptionsArr;
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    function handleOptions(e,index){
        const {name,value}=e.target;
        const oldOptionsArr=props.selectedControl.options||[];
        const changes={...oldOptionsArr[index],[name]:value};
        oldOptionsArr[index]=changes;
        props.selectedControl.options = oldOptionsArr;
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    function handleIcon(iconName, iconFile,index){
        const stepJson = props.selectedControl.children[index];
        stepJson.icon.name = iconName;
        stepJson.icon.file = iconFile;
        props.selectedControl.children[index]=stepJson;
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }

    useEffect(() => {
        if(toAddSubStep) {
            props.selectedControl.children = [];
            updatedComponentInFormState(props.selectedControl.id, props.selectedControl)
            addSubStep(props.selectedControl.StepIndex,true, props.selectedControl);
            setToAddSubStep(false);
        }
    },[toAddSubStep])

    function handleToggle(e,field){
        const value=e.target.value;
        props.selectedControl.style[field] = value;
        // console.log(value, field);
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }
    // function handleInputRadio(e,field){
    //     const value=e.target.value;
    //     props.selectedControl.style[field] = value;
    //     console.log(value, field);
    //     updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    // }
    function deleteOption(index){
        const optionsArr=props.selectedControl.options;
        optionsArr.splice(index,1);
        props.selectedControl.options = optionsArr;
        // console.log(optionsArr);
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
    }
    
    function getSelectedLayout(property, value) {
        // console.log(value);
            changeSectionLayout(value, props.selectedControl);
        }

    const handleDoubleClick = useCallback(debounce(() => {
        addStep();
        }, 200, {leading: true, trailing: false, maxWait: 200}), []);

    const handleDoubleClick2 = useCallback(debounce((index,data) => {
        verifyAddSubStep(index,data);
        }, 200, {leading: true, trailing: false, maxWait: 200}), []);

    function addStep() {
            //console.log('props.selectedControl::', props.selectedControl);
            var controlData = props.selectedControl;
            
            var frameIndex = props.selectedControl && props.selectedControl.frameIndex;
            var extraDetails={};
            if(props.selectedControl && props.selectedControl.elementIndex){
                extraDetails.elementIndex = props.selectedControl.elementIndex;
            }
            // const newId=createUniqueId("step");
            // var newStep={
                // id:newId,
                // elem_type:"step",
                // children:[],
                // style:{},
                // icon:{file:"",name:""},
                // stepName:newId,
                // ColumnLayout:"3"
            // }
            sendRequestForControl( frameIndex, 'add', "step", extraDetails)
            .then((response)=>{
                var newStep = response.data;
                // console.log(newStep) 
                newStep.stepName=newStep.id;
                controlData.children.push(newStep);
                updatedComponentInFormState(controlData.id, controlData);
                setOpenLoader(false);      
            })
            .catch(err =>{
                setOpenLoader(false);
            })
            
                // if (controlData.children !== undefined && controlData.children !== null) {
                //     controlData.children.push(newStep);
                // } else {
                //     controlData.children = [];
                //     controlData.children.push(newStep);
                // }
                // updatedComponentInFormState(controlData.id, controlData); 
        }
        const containsSubsteps = (json, find) =>{
            const findKey=find?.toLowerCase();
            return json?.children?.findIndex(function(child){return child.elem_type.toLowerCase()=== findKey })!==-1
        }
        

        function verifyAddSubStep(index,data) {
            //console.log('props.selectedControl::', props.selectedControl);
            if(data?.children?.length>0 && !containsSubsteps(data,"substep") ) {
                setAlertData({
                    'open':true,
                    'showConfirm':'showconformationDialogBox',
                    'alertContentKey':`${t('Form data from parent step will be lost if substep is created. Do you want to create substep?')}`,
                    'alertTitleKey':`${t('Are you sure want to add Sub Step')}`, 
                    "Button1Key" : 'No',
                    "Button2Key" : 'Yes',
                    'addsubstep' : true
                })
            }
            else if(data?.children?.length === 5 && containsSubsteps(data,"substep")) {
                setAlertData({
                    'open':true,
                    'showConfirm':'showNotification',
                    'alertType':'error',
                    'alertContentKey':`Cannot add more than 5 substeps`,
                    'alertTitleKey':`More substeps cannot be added`, 
                }) 
            }
            else {
                addSubStep(index,false,data);
            }
    }

    const addSubStep = (index,check,data) => {
        var controlData = data;
        var frameIndex = props.selectedControl && props.selectedControl.frameIndex;
        var extraDetails={};
        if(props.selectedControl && props.selectedControl.elementIndex){
            extraDetails.elementIndex = props.selectedControl.elementIndex;
            
        }
        sendRequestForControl( index, 'add', "addSubStep", extraDetails)
        .then((response)=>{
            var newSubstep = response.data;
            const tempData = cloneDeep(controlData);
            // console.log(newStep) 
            newSubstep.substepName=newSubstep.id;
            controlData.children.push(newSubstep);
            // console.log("controlData",controlData);
            updatedComponentInFormState();
            if(check || tempData.children.length === 0) {
                window.changeIndex(controlData.id,newSubstep.id,'0');
            }
            setOpenLoader(false);     
        })
        .catch(err =>{
            setOpenLoader(false);
        })
    }

    const [newSubStepIndex, setNewSubStepIndex] = useState();


    useEffect(() => {
        if(confirmDeleteStep) {
            deleteStep();
            setConfirmDeleteStep(false);
        }
    },[confirmDeleteStep])

    useEffect(() => {
        if(confirmDeleteSubStep) {
            deleteSubStep(newSubStepIndex);
            setConfirmDeleteSubStep(false);
        }
    },[confirmDeleteSubStep])

    function checkDeleteStep() {
        setAlertData({...ControlsMap["customizedAlertConfirmationData"], 'open' : true, 'confirmDeleteStep' : true})
    }
    
    function checkDeleteSubStep(substepIndex) {
        setNewSubStepIndex(substepIndex)
        setAlertData({...ControlsMap["customizedAlertConfirmationData"], 'open' : true, 'confirmDeleteSubStep' : true})
    }

    function deleteStep(){
        var controlData = props.formData?.children?.[1]?.children?.[1];
            if(controlData?.children?.length > 1)
            {                
                var frameIndex = props.selectedControl && props.selectedControl.frameIndex;
                sendRequestForControl( frameIndex, 'delete', "step", {"stepIndex" : props?.selectedControl?.StepIndex, "id": props?.selectedControl?.id})
                .then((response)=>{
                    const stepIndex = props?.selectedControl?.StepIndex;
                    const tempData = cloneDeep(controlData);
                    controlData.children.splice(props?.selectedControl?.StepIndex,1);
                    for(let child of controlData.children) {
                        if(props?.selectedControl?.StepIndex < child.StepIndex) {
                            updateStepIndex(child, child.StepIndex - 1,"stepIndex");
                            child.StepIndex = child.StepIndex - 1;
                        }
                      }
                    updatedComponentInFormState(controlData.id, {...controlData});
                    if(stepIndex === tempData?.children?.length - 1) 
                        window.setIndex(controlData?.children?.[0]?.id,'0',containsSubsteps(controlData?.children?.[0]),controlData?.children?.[0]?.children?.[0]?.id);
                    else 
                        window.setIndex(controlData?.children?.[stepIndex]?.id,stepIndex,containsSubsteps(controlData?.children?.[stepIndex]),controlData?.children?.[stepIndex]?.children?.[stepIndex]?.id);
                    setOpenLoader(false);
                    setOpenProperties(false);
                })
                .catch(err =>{
                     console.log(err);
                    setOpenLoader(false);
                })    
            }
            else if(controlData.children.length===1){
                setAlertData({
                    'open':true,
                    "alertContentMsg": `${t("translation:Last_step_cannot_be_deleted")}`,
                    'showConfirm':'showNotification',
                    'alertType':'error',
                    "autoHideDuration": 6000,
                }) 
            } 
        
    }
    function deleteSubStep(subStepIndex){
        var controlData = props.formData?.children?.[1]?.children?.[1];            
        var frameIndex = props.selectedControl && props.selectedControl.frameIndex;
        sendRequestForControl( frameIndex, 'delete', "subStep", {"stepIndex":props?.selectedControl?.StepIndex,"subStepIndex":subStepIndex, "subChildMenuIndex" : "-1", "id": props.selectedControl?.children?.[subStepIndex]?.id })
        .then((response)=>{
                const length = controlData.children[props?.selectedControl?.StepIndex].children.length;
                controlData.children[props?.selectedControl?.StepIndex].children.splice(subStepIndex,1);
                for(let child of controlData.children[props?.selectedControl?.StepIndex].children) {
                    if(subStepIndex < child.SubStepIndex) {
                        updateStepIndex(child, child.SubStepIndex - 1,"subStepIndex");
                        child.SubStepIndex = child.SubStepIndex - 1;
                    }
                  }
                updatedComponentInFormState(controlData.id, {...controlData});
                if(controlData.children[props?.selectedControl?.StepIndex].children?.length === 0) 
                    window.setIndex(controlData.children[props?.selectedControl?.StepIndex]?.id,props?.selectedControl?.StepIndex);
                else if(subStepIndex === length - 1)
                    window.setIndex(controlData.children[props?.selectedControl?.StepIndex]?.id,props?.selectedControl?.StepIndex,true,controlData.children[props?.selectedControl?.StepIndex].children?.[0]?.id,'0');                
                else 
                    window.setIndex(controlData.children[props?.selectedControl?.StepIndex]?.id,props?.selectedControl?.StepIndex,true,controlData.children[props?.selectedControl?.StepIndex].children?.[subStepIndex]?.id,subStepIndex);
                setOpenLoader(false);
            })
            .catch(err =>{
                 console.log(err);
                setOpenLoader(false);
            })
    }
    function handleStep(e,stepIndex){
        const {name,value}=e.target;
        var controlData = props.selectedControl;            
        controlData[name]=value;
        updatedComponentInFormState(controlData.id, controlData); 
    }
    function handleSubStep(e,stepIndex, subStepIndex){
        const {name,value}=e.target;
        var controlData = props.selectedControl;            
        controlData.children[subStepIndex][name]=value;
        updatedComponentInFormState(controlData.id, controlData); 
    }

    function returnHtml(key,value, type) {
        switch (type.toLowerCase()) {
            case "toggle": return (
                <div className={value.field==="showIcon"? "customToggleBtn1" : "toggleBtn"}>
                    <span> {t(`translation:${ControlsMap[key].label}`)} </span>
                    <ToggleBtn  handleToggle={handleToggle} field={value.field} toggleState={style[value.field]} />
                </div>
            )
            case "toggletype2":
                // console.log("value--",value);
                 return (
                <div className={value.field==="showIcon"? "customToggleBtn1" : "toggleBtn"}>
                    <span> {t(`translation:${value.label}`)} </span>
                    <ToggleBtn  handleToggle={handleToggle} field={value.field} toggleState={style[value.field]} />
                </div>
            )
            case "toggleshort": return (
                <div className={"toggleshort"}>
                    <span> {t(`translation:${value.label}`)} </span>
                    <ToggleBtn  handleToggle={handleToggle} field={value.field} toggleState={style[value.field]} />
                </div>
            )
            case "combo": return (
                <div className="combo" >
                    {t(`translation:${ControlsMap[key].label}`)}
                    <select style={{width:value.field==="zonelist"? "121px" :null}} value={style[value.field]||value.fallback} onChange={(e)=>handleProperty(value.field,e.target.value)} >
                    {ControlsMap[key].children.map((item)=>{
                        return(
                        <option style={item?.style} value={item.Value} > {item.Title} </option>
                        )
                    })}
                    </select>
                </div>                
            )
            case "text": return (
                <div className="text" style={{color:value.color, fontWeight:value.weight}}>
                    {t(`translation:${value.label}`)}
                    
                </div>
            )
            case "line":               
            return (
                <hr className="line" size={value.size} color={value.color} width={value.width} />
            )
            case "colorpicker":  
            return (
                    <>
                    {(style.layout && style.layout.toLowerCase()==="style3" && value.field==="InactiveBorderColor") ||
                     (style.layout && style.layout.toLowerCase()!=="style3" && value.field==="InactiveBackgroundColor")  
                     ? null:
                        <div className="colorpicker">
                        {t(`translation:${value.label}`)}
                        <ColorPicker color={ isColor(style[value.field]) || isColor(globalTheme[value.field]) || isColor(value.fallback)} handleProperty={handleProperty} propertyName={value.field} />
                        </div>
                    }   
                    </>  
                )
            case "textarea":               
            return (
                <div className="textarea">
                {t(`translation:${value.label}`)}                
                <input aria-label={"Control Value1"}autoComplete="off" rows="2" 
                defaultValue={value.field==="label" ? props.selectedControl[value.field] :  props.selectedControl.style[value.field]}
                onBlur={(e)=>handleSupProperty(value.field,e.target.value)} />
                </div>
            )
            case "input":               
            return (<>              
                {(style.resize!=="fullWidth" && (props.selectedControl.style.scale==="pixels" && (value.field==="width" || value.field==="height") )
                ||  (props.selectedControl.style.scale==="percentage" && value.field==="size")) && 
                <div className="input">
                {t(`translation:${value.label}`)}
                <input aria-label={"Control Value2"}autoComplete="off" defaultValue={props.selectedControl.style[value.field]}
                onBlur={(e)=>handleSupProperty(value.field,e.target.value)} />
                </div>
                }                
            </>
            )
            case "longinput":
                return(<>{value.field==="imageUrl" && style.uploadType==="file"? null :
                    <div className="longinput">
                    {t(`translation:${value.label}`)}
                    <input aria-label={"Control Value3"}className="longinput_input" autoComplete="off" rows="2"
                defaultValue={value.field==="label" ? props.selectedControl[value.field] :  props.selectedControl.style[value.field]}
                onBlur={(e)=>handleSupProperty(value.field,e.target.value)} />
                    </div>
                }
                    </>
            )
            case "uploadbtn":
            return(
                <>
                { style.uploadType!=="url" &&
                <div className="uploadbtn">
                {t(`translation:${value.label}`)}
                <ImageUploadBtn
                  containerId={props.containerId} handleImageUpload={handleIcon} iconName={props.selectedControl.style.iconName} />
                </div>
                }
                </>
            )
            case "uploadbtntype2":
            return(
                <>
                {props.selectedControl.style.showIcon && props.selectedControl.style.showIcon==="true" &&
                <div className="uploadbtn">                
                <ImageUploadBtn
                  containerId={props.containerId} handleImageUpload={handleIcon} iconName={props.selectedControl.style.iconName} />
                </div>
                }
                </>
            )
            case "inputcheckbox":
                return(
                    <div className="inputcheckbox">
                        <input aria-label={"Control Value4"} type="checkbox" /> <span> &nbsp; {value}</span>
                    </div>
            )
            case "inputradio":
                return(
                    <div className="inputradio">
                        {value.label.map((item)=>{
                            return(
                            <div>
                            <input aria-label={"Control Value5"} type="radio" id={item.label} name={value.field} checked={item.value===style[value.field]} value={item.value} onClick={(e)=>handleToggle(e,value.field)} />
                            <label aria-label={item.label} htmlFor={item.label}> &nbsp; {t(`translation:${item.label}`)}</label>
                            </div>
                            )
                        })}
                    </div>
            )
            case "longradio":
                return(
                    <>
                        {value.label.map((item)=>{
                            return(
                            <div className="longradio">
                            <input aria-label={"Control Value6"} type="radio" id={item.label} name={value.field} checked={item.value===style[value.field]} value={item.value} onClick={(e)=>handleToggle(e,value.field)} />
                            <label aria-label={item.label} htmlFor={item.label}> &nbsp; {t(`translation:${item.label}`)}</label>
                            {style.resize==="dimension" && item.value==="dimension" && 
                            <select aria-label={item.label} value={style.scale||"pixels"} onChange={(e)=>handleProperty("scale",e.target.value)} style={{ marginLeft:"auto"}}>
                                <option value="pixels"> {t(`translation:${"Pixels"}`)} </option>
                                <option value="percentage"> {t(`translation:${"Percentage"}`)} </option>
                            </select>
                            }
                            {style.transform==="enlarge" && item.value==="enlarge" &&
                            <input aria-label={"Control Value7"} style={{marginLeft:"auto",width:"121px"}} autoComplete="off" defaultValue={props.selectedControl.style[item.value]}
                            onBlur={(e)=>handleSupProperty(item.value,e.target.value)} />
                            }
                            </div>
                            )
                        })}
                    </>
            )
            case "styleoptions": 
            return(
                <ActiveIcon
                labelPosition="in"
                labelStyle={{fontSize:"10px",margin:"5px 0 12px 0"}} layout="3" defaultValue="Style1" 
                spacing="3px" 
                activeStyle={{padding:"0px",background:"#fff",margin:"0",color:"#0072C6", border:"0px  " }}
                inActiveStyle={{padding:"0px",background:"#fff",margin:"0"}} passedValue={style.ControlStyle} property={value.field} handleStyleType={handleStyleType}>
                    <SvgStyle1 value="Style1" label="Style1" />
                    <SvgStyle2 value="Style2" label="Style2" />
                    <SvgStyle3 value="Style3" label="Style3" />
                    <SvgStyle4 value="Style4" label="Style4" />
                </ActiveIcon>
            )
            case "cardlabelvalue":
                return(
                    <>
                        {style.addOptionsMethod==="manually" && (props.selectedControl.options) && (props.selectedControl.options).map((item,count)=>{
                            return(
                                <div className="cardlabelvalue">
                                    <p>Option Label {count+1}</p>
                                    <input aria-label={"Control Value8"} autoComplete="off" placeholder={item.label} defaultValue={item.label} onBlur={(e)=>handleOptions(e,count)} name="label" /><span onClick={()=>deleteOption(count)}> <RedCross /> </span> 

                                    <input aria-label={"Control Value9"} autoComplete="off" placeholder={item.label} value={item.label} onChange={(e)=>handleOptions(e,count)} name="label" /><span onClick={()=>deleteOption(count)}> <RedCross /> </span> 

                                    <input aria-label={"Control Value10"} autoComplete="off" placeholder={item.label} defaultValue={item.label} onBlur={(e)=>handleOptions(e,count)} name="label" /><span onClick={()=>deleteOption(count)}> <RedCross /> </span> 

                                </div>
                            )
                        })}
                    </>
                )
            case "stepssubsteps":
                return(
                    <>
                    {
                                <div key={props.selectedControl.id} className="stepssubstepsMain" onClick={()=>setActiveStep(props.selectedControl.id)} >
                                <div className="cardlabelvalue stepssubsteps">
                                    <p> {t('Step')} {props.selectedControl.StepIndex+1} </p>
                                    <input aria-label={"Control Value11"} autoComplete="off" placeholder={props.selectedControl.stepName} defaultValue={props.selectedControl.stepName} onBlur={(e)=>handleStep(e,props.selectedControl.id)} name="stepName" />
                                    <span onClick={()=>checkDeleteStep()}> <RedCross /> </span>
                                    
                                    {style.showIcons==="true" &&
                                    <div style={{marginRight:"2.3em"}}> 
                                        <ImageUploadBtn
                  containerId={props.containerId} uploadindex={props.selectedControl.id} handleImageUpload={handleIcon} iconName={props.selectedControl.icon.name} />
                                    </div>
                                    }                                   
                                    
                                </div>

                                    <div className="substeps"> 
                                        <p>
                                            <span>{
                                                props.selectedControl?.children.findIndex(function(substep){return substep.elem_type.toLowerCase()==="substep"})!==-1 &&
                                                `${t(`translation:Substep`)}` }</span> 
                                            <span onClick={()=>handleDoubleClick2(props.selectedControl.StepIndex,props.selectedControl)}> <PlusOrange color="#338ED1" /> {t(`translation:Substep`)}</span> 
                                        </p>
                                        {props?.selectedControl.children.map((substep,subStepIndex)=>{
                                        return(
                                            <>
                                            {substep.elem_type.toLowerCase()==="substep" && 
                                            <div key={substep.id} className="subStep">
                                            <input aria-label={"Control Value12"} autoComplete="off" placeholder={substep.substepName} defaultValue={substep.substepName} onBlur={(e)=>handleSubStep(e,props.selectedControl.id,subStepIndex)} name="substepName" />
                                            <span onClick={()=>checkDeleteSubStep(subStepIndex)}> <RedCross /> </span>
                                            </div>
                                            }
                                            </>
                                        )
                                        })}
                                    </div>
                                </div>                            
                        }
                    </>
                )
            case "widebutton":
                return(<> 
                    <div className="widebutton" style={value.style} onClick={() => handleDoubleClick()}> {t(`translation:${value.label}`)} </div>
                </>
                ) 
            case "smalloptionselect":
                return(
                <div className="smalloptionselect">
                    <p>{t(`translation:${value.label}`)}</p>
                    {value.field==="imageAlignment" &&
                <ActiveIcon 
                    labelPosition="in"
                    layout="3" 
                    defaultValue="right" 
                    spacing="3px" 
                    activeStyle={{padding:"0",background:"#fff",margin:"0",color:"#0072C6"}}
                    inActiveStyle={{padding:"0",background:"#fff",margin:"0"}}
                    passedValue={style.imageAlignment} property={value.field} handleStyleType={handleStyleType}>
                    <SvgAlignment3 value="left" />
                    <SvgAlignment2 value="center" />
                    <SvgAlignment1 value="right" />
                </ActiveIcon>
                    }
                    
                </div>
                )
            case "optionsselect":
                return(
                    <> {value.field || (value.field===null && style.uploadType!=="url") ?
                    <ActiveIcon
                    defaultValue="file" 
                    layout={2} 
                    spacing="0"
                    property={value.field}
                    activeStyle={{display:"flex",alignItems:"center",padding:"0",fontWeight:"600",borderRadius: "2px",width:"11em",height:"40px",justifyContent:"center",background:"#fff",color:"#0072C6"}}
                    inActiveStyle={value.field===null ? {background:"#F0F0F0",border:"1px solid #C4C4C4",borderRadius: "2px",width:"26.4em",fontSize:"10px",cursor:"default"}: 
                    {display:"flex",alignItems:"center",fontWeight:"600",padding:"0",width:"11em",borderRadius: "2px",height:"40px",background:"#fff",justifyContent:"center",color:"#C4C4C4",border:"1px solid #C4C4C4"}} 
                    passedValue={style[value.field]} 
                    handleStyleType={handleStyleType}
                    >
                    {value.label.map((item)=>{
                        return(
                            <div value={item.value}>
                        { item.value!==null && item.value==="file" && <SvgImport color={style[value.field]==="file" || style[value.field]===undefined || style[value.field]==="" ? "#0072C6" : "#C4C4C4"} />}
                        { item.value!==null && item.value==="url" && <SvgUpload color={style[value.field]==="url"? "#0072C6" : "#C4C4C4"} />} &nbsp;&nbsp;     
                        <span value={item.value}>{t(`translation:${item.label}`)}</span>
                        </div>
                        )
                    })}
                        {/* <span style={{margin:"0 8px 0 8px"}} value="manually">Create Manually</span>
                        <span value="database">Fetch from Database</span> */}
                    </ActiveIcon>
                    : null}
                    </>)
            case "layout":
                        return(
                            <>
                <ActiveIcon 
                    labelPosition="in"
                    layout="3" 
                    defaultValue="style1"
                    spacing="0"
                    labelStyle={{fontSize:"14px"}}
                    activeStyle={{margin:"0",background:"#fff",border: "1px solid #0072c6",color:"#0072C6"}} 
                    inActiveStyle={{margin:"0",background:"#fff",border: "1px solid #c6c9cf"}}
                    passedValue={style[value.field]}
                    property={value.field}
                    handleStyleType={handleStyleType}
                >
                    {value.label.map((item)=>{
                        return(
                            <ImageRenderer value={item.value} label={item.label} >
                                <img style={{width:value.width}} src={require(`../../../../Icons/${item.icon}`).default} alt={item.value} />
                            </ImageRenderer>
                        )
                    })}
                </ActiveIcon>
                            </>
                            ) 
            case "databaseQuery":
                return(
                    <>
                    
                    </>
                    )                   
        }
    }

    // function handleLayout(value){
    //     () =>getSelectedLayout(data.id+'FrameLayout',data.id)
    // }

    // console.log(props);

    return (
        <>
        <ControlLabel label={ props.formData?.containerControl === 'sidenavigation' ? `Tabs-vertical / ${props.selectedControl.id}` : `Wizard / ${props.selectedControl.id}`} handleDelete={deleteControl} id={props.selectedControl.id} />
        <div className="propertiesBody">
            {
                CollapsibleItems.map((item,index)=>{
                    return item;
                })
            }
        </div>
        {
              alertData.open === true &&
                <CustomizedAlert alertData={alertData} setConfirmDeleteStep={setConfirmDeleteStep} setConfirmDeleteSubStep={setConfirmDeleteSubStep} setToAddSubStep={setToAddSubStep}/>
            }
        </>
    )
}

export default StepProperties
